<template>
  <div>
    <b-row>
      <b-col cols="12" xl="4">
        <b-row>
          <b-col cols="8" xl="7"
            ><span class="title-fw"
              >{{ $t("CURRENTEXCELLENTPOINT") }}：</span
            ></b-col
          >
          <b-col cols="4" xl="1">1000P</b-col>
        </b-row>
        <b-row>
          <b-col cols="12" xl="10">
            <label class="text-danger pl-2"
              >内200Pが2020/09/29に失効期限を迎えます。</label
            >
          </b-col>
        </b-row>
      </b-col>
      <b-col cols="12" xl="6">
        <b-row>
          <b-col cols="12" xl="3">
            <label class="title-fw title-fs">{{ $t("PROCESSINGDAY") }}</label>
          </b-col>
          <b-col cols="12" xl="4">
            <b-input-group>
              <b-form-input
                v-model="processingDayFrom"
                type="text"
                autocomplete="off"
              ></b-form-input>
              <b-input-group-append>
                <b-form-datepicker
                  v-model="processingDayFrom"
                  @context="onContext"
                  button-only
                  right
                ></b-form-datepicker>
              </b-input-group-append>
            </b-input-group>
          </b-col>
          <b-col cols="12" xl="1" class="pr-0">～</b-col>
          <b-col cols="12" xl="4">
            <b-input-group>
              <b-form-input
                v-model="processingDayTo"
                type="text"
                autocomplete="off"
              ></b-form-input>
              <b-input-group-append>
                <b-form-datepicker
                  v-model="processingDayTo"
                  @context="onContext"
                  button-only
                  right
                ></b-form-datepicker>
              </b-input-group-append>
            </b-input-group>
          </b-col>
        </b-row>
        <b-row class="d-flex align-items-center">
          <b-col cols="12" xl="3">
            <label class="title-fw title-fs">{{
              $t("HISTORYCLASSIFICATION")
            }}</label>
          </b-col>
          <b-col cols="12" xl="4">
            <b-form-select
              :plain="true"
              :options="historyClassificationOptions"
              value="獲得"
            ></b-form-select>
          </b-col>
          <b-col cols="12" xl="1"></b-col>
          <b-col cols="12" xl="4">
            <b-button variant="primary">{{ $t("NARROWDOWN") }}</b-button>
          </b-col>
        </b-row>
      </b-col>
      <b-col cols="12" xl="2"></b-col>
    </b-row>

    <!-- ポイント獲得・消費履歴一覧 -->
    <hr class="hrBarType" />
    <b-table
      ref="table"
      :fields="fields"
      :items="tableData"
      sticky-header
      responsive="sm"
    >
      <template v-slot:cell(Item1)="item">
        <label :class="{ 'text-danger': item.value.isNegative }">{{
          item.value.value
        }}</label>
      </template>
      <template v-slot:cell(Item2)="item">
        <label :class="{ 'text-danger': item.value.isNegative }">{{
          item.value.value
        }}</label>
      </template>
      <template v-slot:cell(Item3)="item">
        <label :class="{ 'text-danger': item.value.isNegative }">{{
          item.value.value
        }}</label>
      </template>
      <template v-slot:cell(Item4)="item">
        <label :class="{ 'text-danger': item.value.isNegative }">{{
          item.value.value
        }}</label>
      </template>
      <template v-slot:cell(Item5)="item">
        <label :class="{ 'text-danger': item.value.isNegative }">{{
          item.value.value
        }}</label>
      </template>
    </b-table>

    <!-- ページャー＆表示件数 -->
    <br />
    <b-row>
      <b-col cols="12" xl="3"></b-col>
      <b-col cols="12" xl="5">
        <b-pagination
          id="message-list"
          v-if="tableData.length != 0"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          align="center"
        ></b-pagination>
      </b-col>
      <b-col cols="12" xl="3">
        <div class="countList" align="right">
          <label>{{ $t("DISPLAY") }}</label
          >&nbsp;&nbsp;
          <label>50 | </label>
          <b-link href="javascript:;">100 | </b-link>
          <b-link href="javascript:;">150</b-link>
        </div>
      </b-col>
      <b-col cols="12" xl="1"></b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      processingDayFrom: "",
      processingDayTo: "",
      formatted: "",
      selected: "",
      historyClassificationOptions: this.getHistoryClassificationList(),
      tableData: [],
      currentPage: 1,
      perPage: 2,
    };
  },
  computed: {
    rows() {
      return this.tableData.length;
    },
    lists() {
      const items = this.tableData;
      return items.slice(
        (this.currentPage - 1) * this.perPage,
        this.currentPage * this.perPage
      );
    },
    fields() {
      return [
        {
          key: "Item1",
          label: this.$t("PROCESSINGDATE"),
          class: "text-center normal-width",
        },
        {
          key: "Item2",
          label: this.$t("HISTORYCLASSIFICATION"),
          class: "text-center normal-width",
        },
        {
          key: "Item3",
          label: this.$t("POINTS"),
          class: "text-center normal-width",
        },
        {
          key: "Item4",
          label: this.$t("STAGE"),
          class: "text-center normal-width",
        },
        {
          key: "Item5",
          label: this.$t("POINTSEARNEDCONSUMED"),
          class: "text-center normal-width",
        },
      ];
    },
  },
  mounted() {
    this.getTableData();
  },
  methods: {
    onContext(ctx) {
      this.formatted = ctx.selectedFormatted;
      this.selected = ctx.selectedYMD;
    },
    getHistoryClassificationList() {
      return ["獲得", "消費", "失効"];
    },
    getTableData() {
      this.tableData = [
        {
          Item1: {
            value: "2020/07/31 11:23",
            isNegative: false,
          },
          Item2: {
            value: "獲得",
            isNegative: false,
          },
          Item3: {
            value: "15",
            isNegative: false,
          },
          Item4: {
            value: "エクセレント",
            isNegative: false,
          },
          Item5: {
            value: "電気代削減の見積依頼",
            isNegative: false,
          },
        },
        {
          Item1: {
            value: "2020/07/22 09:41",
            isNegative: false,
          },
          Item2: {
            value: "獲得",
            isNegative: false,
          },
          Item3: {
            value: "20",
            isNegative: false,
          },
          Item4: {
            value: "エクセレント",
            isNegative: false,
          },
          Item5: {
            value: "労務関連補助金の査定依頼",
            isNegative: false,
          },
        },
        {
          Item1: {
            value: "2020/07/15 15:44",
            isNegative: false,
          },
          Item2: {
            value: "獲得",
            isNegative: false,
          },
          Item3: {
            value: "100",
            isNegative: false,
          },
          Item4: {
            value: "グレイト",
            isNegative: false,
          },
          Item5: {
            value: "ステージアップ：グレイト➞エクセレント",
            isNegative: false,
          },
        },
        {
          Item1: {
            value: "2020/06/17 17:11",
            isNegative: true,
          },
          Item2: {
            value: "消費",
            isNegative: true,
          },
          Item3: {
            value: "-500",
            isNegative: true,
          },
          Item4: {
            value: "グレイト",
            isNegative: true,
          },
          Item5: {
            value: "ポイントプログラムAAAAの応募",
            isNegative: true,
          },
        },
        {
          Item1: {
            value: "2020/06/06 14:35",
            isNegative: false,
          },
          Item2: {
            value: "獲得",
            isNegative: false,
          },
          Item3: {
            value: "15",
            isNegative: false,
          },
          Item4: {
            value: "グレイト",
            isNegative: false,
          },
          Item5: {
            value: "仕訳帳インポート",
            isNegative: false,
          },
        },
        {
          Item1: {
            value: "2020/06/05 00:00",
            isNegative: true,
          },
          Item2: {
            value: "失効",
            isNegative: true,
          },
          Item3: {
            value: "-25",
            isNegative: true,
          },
          Item4: {
            value: "グレイト",
            isNegative: true,
          },
          Item5: {
            value: "失効期限",
            isNegative: true,
          },
        },
      ];
      this.tableData.forEach((element, index) => {
        if (index === this.tableData.length - 1) {
          this.tableData[index].last_element = true;
          return;
        }
      });
    },
  },
};
</script>

<style>
.hrBarType {
  height: 2px;
  border-width: 10;
  color: gray;
  background-color: gray;
}
</style>